import { BehaviorSubject, Subject } from 'rxjs';
import { propDialog } from './type';

class KYCModalStore {
  _openModal = new Subject<propDialog>()
  _requirePermission = new BehaviorSubject<RequirePermissionType>({})

  set openModal(value: propDialog) {
    this._openModal.next(value)
  }

  get requirePermission(){
    return this._requirePermission.getValue()
  }

  set requirePermission(value: RequirePermissionType){
    this._requirePermission.next(value)
  }
}

interface RequirePermissionType {
  isRequire?: boolean,
  callback?: () => void
}

const kycModalStore = new KYCModalStore()

export default kycModalStore