import { Button, Typography } from '@mui/material'
import { bridge } from 'app/communicateBridge'
import Modal from 'components/Modal'
import { ModalHandle } from 'components/Modal/types'
import { FlexBox } from 'components/styled'
import { useEffect, useRef } from 'react'
import { useTranslation } from 'react-i18next'
import kycModalStore from '../components/store/kycModalStore'
import Row, { RowProps } from './Row'

const RequirePermissionModal = () => {
  const modalRef = useRef<ModalHandle>(null)

  const { t } = useTranslation()

  const arr: Array<RowProps> = [
    {
      index: 1,
      image: 'images/settingIcon.svg',
      value: [t('permission.goSetting.1'), t('permission.goSetting.2'), t('permission.goSetting.3')]
    },
    {
      index: 2,
      image: 'images/appIcon.svg',
      value: [t('permission.findPayME.1'), t('permission.findPayME.2')]
    },
    {
      index: 1,
      image: 'images/cameraIcon.svg',
      value: [t('permission.grandCamera.1'), t('permission.grandCamera.2')]
    }
  ]

  useEffect(() => {
    const subscription = kycModalStore._requirePermission.subscribe(value => {
      if (value.isRequire) {
        modalRef.current?.open?.()
      } else {
        modalRef.current?.close?.()
      }
    })

    return () => {
      subscription.unsubscribe()
    }
  }, [])

  return (
    <Modal
      ref={modalRef}
      title={t('permission.title')}
    >
      <FlexBox pt={2} />
      <Typography variant='overline' fontSize={14} color='text.caption'>
        {t('permission.subtitle1')}
        <Typography variant='overline' fontSize={14} color='text.caption' fontWeight='600'>
          {t('permission.subtitle2')}
        </Typography>
        {t('permission.subtitle3')}
      </Typography>
      <FlexBox pt={2} />
      {
        arr.map(e => <Row
          key={e.index}
          {...e}
        />)
      }
      <FlexBox pt={2} />
      <FlexBox width='100%'>
        <Button variant='outlined' fullWidth onClick={() => modalRef.current?.close?.()} sx={{ height: 48, borderRadius: 2.5 }} >{t('permission.skip')}</Button>
        <FlexBox pr={2.5} />
        <Button
          variant='contained'
          fullWidth
          onClick={() => {
            modalRef.current?.close?.()
            kycModalStore.requirePermission = {
              isRequire: false
            }
            bridge.next({
              type: 'jsOpenSetting',
              data: {}
            })
          }}
          sx={{ height: 48, borderRadius: 2.5 }}>{t('permission.allow')}</Button>
      </FlexBox>
    </Modal >
  )
}


export default RequirePermissionModal
