import { Box, Button, Typography } from '@mui/material'
import { selectDeviceInfo } from 'app/app.slice'
import { bridge } from 'app/communicateBridge'
import { useAppSelector } from 'app/hooks'
import Header from 'components/Layout/Header'
import { LayoutSafeArea } from 'components/Layout/LayoutSafeArea'
import Meta from 'components/Meta'
import { FlexBox, FullSizeFlexBox } from 'components/styled'
import React, { useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import { useNavigate } from 'react-router-dom'
import CheckRow from './CheckRow'
import ListRow from './ListRow'
import kycModalStore from './store/kycModalStore'
import { jsLog } from 'utils/comunicate'
import RequirePermissionModal from '../RequirePermissionModal'
import { isBlurImage } from 'utils/format-utils'

function KYCIntroduce() {
  const deviceInfo = useAppSelector(selectDeviceInfo)
  const navigate = useNavigate()
  const { t } = useTranslation()

  useEffect(() => {
    jsLog(isBlurImage('images/photo_2022-10-10_15-30-10.jpg'))
  }, [])

  window.nativePermissionStatus = (data: NativePermissionStatusProps) => {
    if (data.type === 'CAMERA') {
      if (data.state === 'BLOCKED') {
        kycModalStore.requirePermission = {
          isRequire: true
        }
      } else {
        kycModalStore.requirePermission = {
          isRequire: false
        }
      }
    }
  }

  return (
    <LayoutSafeArea>
      <Meta title='KYC Introduce' />
      <FullSizeFlexBox
        sx={{
          padding: 2.5,
          paddingTop: deviceInfo?.platform?.toUpperCase() === 'IOS' || deviceInfo?.platform?.toUpperCase() === 'ANDROID' ? 0 : 2.5,
          paddingBottom: deviceInfo?.platform?.toUpperCase() === 'IOS' || deviceInfo?.platform?.toUpperCase() === 'ANDROID' ? 0 : 2.5,
          overflow: 'scroll',
          scrollbar: 'none',
        }}
        display="flex"
        flexDirection="column"
        justifyContent="space-between"
      >
        <Box>
          <Header
            isIconBack
            onClick={() => navigate(-1)}
          />
          <Box sx={{ paddingTop: 2.5 }} />
          <Typography variant='h2'>{t('kycIntroduce.title')}</Typography>
          <Box sx={{ paddingTop: 1.5 }} />
          <CheckRow title={t('kycIntroduce.check1')} />
          <CheckRow title={t('kycIntroduce.check2')} />
          <CheckRow title={t('kycIntroduce.check3')} />
          <Box sx={{ paddingTop: 2.5 }} />
          <Typography variant='h4' textAlign='left'>{t('kycIntroduce.noteTitle')}</Typography>
          <Box sx={{ paddingTop: 1 }} />
          <ListRow title={t('kycIntroduce.note1')} number='1' />
          <ListRow title={t('kycIntroduce.note2')} number='2' />
          <ListRow title={t('kycIntroduce.note3')} number='3' />
          <ListRow title={t('kycIntroduce.note4')} number='4' />
        </Box>
        <FlexBox flexDirection='column' width='100%'>
          <Button
            variant='contained'
            onClick={() => {
              kycModalStore.requirePermission = {
                isRequire: true
              }
              bridge.next({
                type: 'jsRequestCardKYC',
                data: {
                  title: 'Chụp mặt trước CMND/CCCD',
                  type: 'FRONT',
                  description: 'Cân chỉnh mặt trước CMND/CCCD vào giữa khung.',
                }
              })
              jsLog('jsRequestCardKYC')
            }}
          >{t('kycIntroduce.button')}</Button>
        </FlexBox>
        <RequirePermissionModal />
      </FullSizeFlexBox>
    </LayoutSafeArea>
  )
}

export default KYCIntroduce