import { Typography } from '@mui/material'
import { FlexBox } from 'components/styled'
import Image from 'mui-image'
import React from 'react'

function Row({ image, index, value }: RowProps) {
  return (
    <FlexBox
      width='100%'
      alignItems='center'
      pt={1.25}
      pb={1.25}
    >
      <Image src={image} width={24} height='auto' fit='cover' duration={0} />
      <FlexBox pr={1} />
      <Typography variant='overline' fontSize={14} color='text.caption'>
        {`${index}. `}{value?.[0] || ''}{' '}
        <Typography variant='overline' fontSize={14} color='text.caption' fontWeight='600'>
          {value?.[1] || ''}{' '}
        </Typography>
        {value?.[2] || ''}
      </Typography>
    </FlexBox>
  )
}

export type RowProps = {
  image: string,
  index: number,
  value: Array<string>
}

export default Row